import React from 'react';
import styled from 'styled-components';
import { withRouter } from 'react-router-dom';
import { ButtonDanger } from './elements';
import InputDatalist from './InputDatalist';
import { GET_ALL_CATEGORIES } from '../client/query';
import { useQuery } from '@apollo/client';
import CategoriesList from './CategoriesList';

const CategoriesFeed = props => {
  const { category, setIdCat, isHide, categoriesWrapper, setHide } = props;
  const { data, loading } = useQuery(GET_ALL_CATEGORIES);
  const isRoot = category.ancestors <= 1;
  const hrefBack =
    category.directAncestor === process.env.CAT_ID
      ? '/'
      : `/category/${category.directAncestor}`;

  const onBack = event => {
    setIdCat(category.directAncestor);
  };

  const goToLink = path => {
    props.history.push(path);
  };
  const goToCategory = cat => {
    setIdCat(cat.descendants.length ? cat._id : cat.directAncestor);
    setHide(true);
    goToLink(`/category/${cat._id}`);
  };

  const categoriesList = loading ? [] : data.allCategories;

  return (
    <Wrapper isHide={isHide}>
      <InputDatalist
        loading={loading}
        list={categoriesList}
        labelText={'Search category'}
        inputPlaceholder={'Title'}
        actionWithReturnValue={cat => {
          goToCategory(cat);
        }}
        actionWithNewValue={title => {
          const targetCat = categoriesList.find(cat => cat.title === title);
          if (targetCat) {
            goToCategory(targetCat);
          }
        }}
        actionIcon="&#9998;"
      />
      {!isRoot && (
        <ButtonBack
          onClick={() => {
            onBack();
            goToLink(hrefBack);
          }}
        >
          Back
        </ButtonBack>
      )}
      <CategoriesList
        catsList={category.descendants}
        goToLink={goToLink}
        setIdCat={setIdCat}
        categoriesWrapper={categoriesWrapper}
      />
    </Wrapper>
  );
};

export default withRouter(CategoriesFeed);

const Wrapper = styled.ul`
  list-style-type: none;
  @media (max-width: 450px) {
    display: ${props => (props.isHide ? 'none' : 'block')};
  }
`;

const ButtonBack = styled(ButtonDanger)`
  flex: auto;
  width: 100%;
`;
