import React, { useEffect, useRef } from 'react';
import { NavLink, withRouter } from 'react-router-dom';
import styled from 'styled-components';
import { ButtonAsLink, ButtonNegative } from './elements';
import useAlert from '../hooks/useAlert';
import useViewCategory from '../hooks/useViewCategory';
import useUserContext from '../hooks/useUserContext';
import { NEUTRAL } from '../const';

const Navigation = props => {
  const { isHide, setHide } = props;
  const navRef = useRef();
  useEffect(() => {
    !isHide && navRef.current.focus();
  });
  const { setInitialViewCategory } = useViewCategory();
  const { logOut } = useUserContext();
  const { setAlert } = useAlert();
  const onlogOut = event => {
    logOut();
    setAlert('Пока! Заходи еще!😩', NEUTRAL);
    props.history.push('/');
  };

  return (
    <Nav
      ref={navRef}
      tabIndex={0}
      isHide={isHide}
      onBlur={event => {
        if (!event.currentTarget.contains(event.relatedTarget)) setHide(true);
      }}
    >
      <ButtonToggle
        onClick={() => {
          setHide(!isHide);
        }}
      >
        Menu
      </ButtonToggle>
      <ul
        onClick={() => {
          setHide(true);
        }}
      >
        <li
          onClick={() => {
            setInitialViewCategory();
          }}
        >
          <NavLink to="/">Home</NavLink>
        </li>
        <li>
          <NavLink to="/my">My Things</NavLink>
        </li>
        <li>
          <NavLink to="/new">New</NavLink>
        </li>
        {props.isLoggedIn ? (
          <li className="last">
            <ButtonAsLink onClick={onlogOut}>Log out</ButtonAsLink>
          </li>
        ) : (
          <>
            <li className="last">
              <NavLink to="/signin">Sign In</NavLink>
            </li>
            <li>
              <NavLink to="/signup">Sign Up</NavLink>
            </li>
          </>
        )}
      </ul>
    </Nav>
  );
};

export default withRouter(Navigation);

const Nav = styled.nav`
  ul {
    position: relative;
    display: flex;
    justify-content: start;
    flex-direction: row;
    list-style-type: none;
  }

  li {
    padding: 0 1em;
    transition: all 2s ease;
  }
  li.last {
    margin-left: auto;
  }
  li a,
  li button {
    text-decoration: none;
    font-size: 1.3em;
    color: hsl(40, 80%, 80%);
    display: block;
  }
  li a:hover {
    text-decoration: underline;
    color: hsl(40, 80%, 70%);
  }

  @media (max-width: 450px) {
    position: relative;
    ul {
      display: ${props => (props.isHide ? 'none' : 'flex')};
      position: absolute;
      top: 2em;
      flex-direction: column;
      width: 100%;
      border-bottom: 1px solid hsl(230deg 30% 60%);
      border-bottom-left-radius: 0.2em;
      border-bottom-left-radius: 0.2em;
      li {
        background: linear-gradient(
          to right,
          hsl(221deg 29% 79%),
          hsl(226deg 29% 72%)
        );
        cursor: pointer;
        &:hover {
          background: linear-gradient(
            to right,
            hsl(221deg 40% 70%),
            hsl(226deg 29% 72%)
          );
        }
        &.last {
          margin: 0;
        }
        & > * {
          color: #fff;
          font-size: 1.3em;
        }
      }
      li + li {
        border-top: 1px solid hsl(230deg 30% 60%);
      }
    }
  }
  @media (orientation: landscape) and (max-height: 450px) and (min-width: 450px) {
    position: relative;
    display: flex;
    flex-direction: column;
    ul {
      background: linear-gradient(
        to right,
        hsl(221deg 29% 79%),
        hsl(226deg 29% 72%)
      );
      display: ${props => (props.isHide ? 'none' : 'flex')};
      font-size: 0.8em;
      position: absolute;
      width: 100%;
      top: 2px;
      border-bottom: 1px solid black;
    }
  }
`;

const ButtonToggle = styled(ButtonNegative)`
  display: none;
  @media (max-width: 450px) {
    display: block;
    width: 100%;
  }
  @media (orientation: landscape) and (max-height: 450px) and (min-width: 450px) {
    align-self: center;
    display: block;
    margin-top: -2.2em;
    background: transparent;
  }
`;
