import React from 'react';
import styled from 'styled-components';
import { ButtonPositive, ButtonSecondary } from './elements';

const CategoriesList = props => {
  const { catsList, goToLink, setIdCat, categoriesWrapper } = props;

  return (
    <List>
      {catsList.map(cat => {
        const isAncestor = !!cat.descendants.length;
        return isAncestor ? (
          <Node
            key={cat._id}
            onClick={() => {
              categoriesWrapper.focus();
              setIdCat(cat._id);
            }}
          >
            <ButtonPositive onClick={() => goToLink(`/category/${cat._id}`)}>
              {cat.title}
            </ButtonPositive>
          </Node>
        ) : (
          <Vertex key={cat._id}>
            <ButtonSecondary onClick={() => goToLink(`/category/${cat._id}`)}>
              {cat.title}
            </ButtonSecondary>
          </Vertex>
        );
      })}
    </List>
  );
};

export default CategoriesList;
const List = styled.ul`
  @media (max-width: 450px) {
    max-height: 20vh;
    overflow-y: auto;
  }
`;
const Node = styled.li`
  display: flex;
  font-weight: bold;
  cursor: pointer;
`;
const Vertex = styled.li`
  display: flex;
`;
