import React from 'react';
import styled from 'styled-components';

const ConstrainPointer = props => {
  const { total, current, message, position, top, left, right, bottom } = props;
  return (
    <Wrapper
      className="constrain-pointer"
      attention={current >= total}
      position={position}
      top={top}
      left={left}
      right={right}
      bottom={bottom}
    >
      {message} {current} / {total}{' '}
    </Wrapper>
  );
};

export default ConstrainPointer;

const Wrapper = styled.span`
  display: inline-block;
  color: ${({ attention }) => attention && 'hsl(21deg 40% 50%)'}};
  text-shadow: ${({ attention }) => attention && '0px 1px 6px hsla(21deg 40% 50% / 0.5)'}};
  position: ${({ position }) => position || 'relative'};
  ${({ top }) => top && `top : ${top}`};
  ${({ right }) => right && `right : ${right}`};
  ${({ bottom }) => bottom && `bottom : ${bottom}`};
  ${({ left }) => left && `top : ${left}`};
`;
