import React, { useState, useRef } from 'react';
import styled from 'styled-components';
import { useQuery } from '@apollo/client';
import { GET_CATEGORY } from '../client/query';

import CategoriesFeed from './CategoriesFeed';
import ButtonSecondary from './elements/ButtonSecondary';
import LoadCategoryFeed from './loaders/loadCategoryFeed';

import useViewCategory from '../hooks/useViewCategory';

const Categories = () => {
  const { viewCategory, setViewCategory } = useViewCategory();
  const wrapperRef = useRef();
  const [isHide, setHide] = useState(true);
  const { data, loading, error } = useQuery(GET_CATEGORY, {
    variables: { id: viewCategory }
  });
  if (error) return <p>The data for displaying "Categories" was not received from the server</p>;
  return (
    <Wrapper
      className="wert"
      tabIndex={0}
      ref={wrapperRef}
      onBlur={event => {
        if (!event.currentTarget.contains(event.relatedTarget)) setHide(true);
      }}
    >
      <ButtonToggle
        onClick={() => {
          setHide(!isHide);
        }}
      >
        {loading ? 'Loading...' : data.category.title} &#9776;
      </ButtonToggle>
      {loading ? (
        <LoadCategoryFeed isHide={isHide} />
      ) : (
        <CategoriesFeed
          setHide={setHide}
          isHide={isHide}
          category={data.category}
          setIdCat={setViewCategory}
          categoriesWrapper={wrapperRef.current}
        />
      )}
    </Wrapper>
  );
};

export default Categories;

const Wrapper = styled.div`
  overflow-x: hidden;
  height: 100%;
  & > * {
    width: 100%;
  }
`;

const ButtonToggle = styled(ButtonSecondary)`
  display: none;
  @media (max-width: 450px) {
    display: block;
  }
`;
